html,body,#root,.App{
  margin:0;
  padding:0;
  text-align: center;
  width:100%;
  height:100%;
  font-family: 'Playfair Display', serif;
  background-color:var(--paper);
}
:root{
  --black:#0A1612;
  --denim:#1A2930;
  --gold:#F7CE3E;
  --paper:#F4F4F4;
  --charcoal:#373737;
}
/* Landing Page */
#landing-page{
  position: relative;
  width:100%;
  height:100%;
  overflow: hidden;
  background-color:var(--paper);
}
#landing-about{
  margin-top:2em;
}
#landing-headshot{
  width: 12.5vmax;
  height: 13vmax;
  display: inline-block;
  float: left;
  margin: 0 3vmax;
  border-radius: 7px;
  overflow: hidden;
}
#landing-headshot img{
  width: 106%;
  height: 105%;
}
#about-info{
  text-align: left;
  display: inline-block;
  float: left;
  margin-right:3vmax;
  color:var(--charcoal);
}
#about-info h1{
  font-size:2vmax;
  margin:15px 0;
}
#about-info h2{
  margin:0 0 5px 0;
  font-size:1.5vmax;
}
#about-text{
  font-size: 1.4vmax;
  text-align: left;
  width:99%;
  -webkit-animation: fadeinright 1s;
          animation: fadeinright 1s;
  color:var(--charcoal);
}

#landing-skills{
  margin-top: 6vmax;
}
.skills-list{
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-grid;
  width: 33.3%;
}
.skill-label{
  font-size:2.6vmax;
  margin-bottom:1vmax;
  margin-top:0.5vmax;
  -webkit-animation: fadeinleft 1.2s;
          animation: fadeinleft 1.2s;
  color:var(--charcoal);
  font-family: 'Oswald', sans-serif;
}
.skill-item{
  font-size:1.6vmax;
  margin-bottom:0.5vmax;
  -webkit-animation: fadeinleft 1.2s;
          animation: fadeinleft 1.2s;
  color:var(--charcoal);
}
#landing-portfolio-btn{
  width: 100%;
  position: absolute;
  bottom:4vmax;
  -webkit-transition:all 1s ease;
  -o-transition:all 1s ease;
  transition:all 1s ease;
  color:var(--charcoal);
}
#landing-portfolio-btn a{
  position: relative;
  bottom:0;
  border:2px solid;
  padding:1.25vmax 3vmax;
  cursor: pointer;
  height:100%;
  width:100%;
  -webkit-transition:all 0.6s ease;
  -o-transition:all 0.6s ease;
  transition:all 0.6s ease;
  font-size:1.5vmax;
  font-family: 'Oswald', sans-serif;
  letter-spacing: 1px;
  -webkit-animation:fadeinbottom 1s;
          animation:fadeinbottom 1s;
}
#landing-portfolio-btn a:hover{
  bottom:1vmax;
  color:var(--gold);
}
/*Projects Page*/
#projects-page{
  width:100%;
  height:100%;
  background-color:var(--paper);
  display:table;
}
.projects-wrapper{
  list-style: none;
  padding:0;
  display: table-cell;
  vertical-align: middle;
}
.projects-item{
  display:inline-block;
  width:40%;
  margin:2vmax;
  height:35vh;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.projects-item img{
  width:100%;
  height:100%;
}
.projects-item div{
  position: absolute;
  left:0;
  top:0;
  width:102%;
  height:100%;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  display: table;
}
.projects-item div h1{
  opacity: 0;
  -webkit-transition: all 0.75s ease;
  -o-transition: all 0.75s ease;
  transition: all 0.75s ease;
  font-family: 'Oswald', sans-serif;
  letter-spacing: 1.2px;
  font-size:2.5vmax;
  display:table-cell; 
  vertical-align:middle;
  color:var(--paper);
}
.projects-item div:hover > h1,.projects-item div:hover{
  background-color:var(--gold);
  opacity: 1;
}
/*Mobile Display*/
@media (max-width: 700px) {
  
}
/*Desktop Display*/
@media (min-width: 1280px) {
  /*Landing Page*/
  #landing-page{
    max-width: 1380px;
    display:inline-block;
  }
  #landing-headshot{
    max-width: 172.250px;
    max-height: 179.125px;
    margin: 0 2.5em;
  }
  #about-info h1{
    font-size: 30px;
    margin: 15px 0;
  }
  #about-info h2{
    margin: 0 0 5px 0;
    font-size: 20px;
  }
  #about-text{
    font-size:18px;
  }
  #landing-skills{
    margin-top:5vh;
    font-size:0;
  }
  .skill-label{
    font-size:33px;
  }
  .skill-item{
    font-size:22px;
  }
  #landing-portfolio-btn{
    bottom:45px;
  }
  #landing-portfolio-btn a{
    padding:17.25px 41.3px;
    font-size:20px;
  }
}
@media (min-width: 1000px) and (max-height: 750px){
  /*Landing Page*/
  #landing-about{
    margin-top:0.5em;
  }
  #landing-skills{
    margin-top:23px;
  }
  .skill-label{
    margin:5px 0;
  }
  .skill-item{
    margin: 2.5px 0;
  }
  #landing-portfolio-btn{
    bottom:20px;
  }
  #landing-portfolio-btn  a{
    padding:8.5px 30.5px;
  }
}
/*Animations*/
@-webkit-keyframes fadeinright{
  0%{
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
  }
  100%{
    -webkit-transform:translateX(0%);
            transform:translateX(0%)
  }
}
@keyframes fadeinright{
  0%{
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
  }
  100%{
    -webkit-transform:translateX(0%);
            transform:translateX(0%)
  }
}
@-webkit-keyframes fadeinleft{
  0%{
    -webkit-transform: translateX(-135%);
            transform: translateX(-135%);
  }
  100%{
    -webkit-transform:translateX(0%);
            transform:translateX(0%)
  }
}
@keyframes fadeinleft{
  0%{
    -webkit-transform: translateX(-135%);
            transform: translateX(-135%);
  }
  100%{
    -webkit-transform:translateX(0%);
            transform:translateX(0%)
  }
}
@-webkit-keyframes fadeintoview{
  0%{
    visibility: hidden;
    opacity: 0;
  }
  100%{
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fadeintoview{
  0%{
    visibility: hidden;
    opacity: 0;
  }
  100%{
    visibility: visible;
    opacity: 1;
  }
}
@-webkit-keyframes fadeinbottom{
  0%{
    bottom:-15vw;
  }
  100%{
    bottom:0;
  }
}
@keyframes fadeinbottom{
  0%{
    bottom:-15vw;
  }
  100%{
    bottom:0;
  }
}


